/* AccountForm.css */
.account-form-container {
    display: flex;
    flex-direction: column;
    width: 400px; /* Adjust as needed */
    max-width: 90%; /* Prevent form from becoming too wide on smaller screens */
    margin: 50px auto; /* Center the form */
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    background-color: #f8f8f8;
}

.form-group {
    margin-bottom: 15px;
}

.form-label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

.form-input {
    width: calc(100% - 12px); /* Account for padding and border */
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

.form-submit-button {
    background-color: #007bff; /* Blue */
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    width: 100%;
    transition: background-color 0.3s ease, opacity 0.3s ease; /* Add opacity transition */
}

.form-submit-button:hover {
    background-color: #0056b3; /* Darker blue on hover */
}

.form-submit-button.submitting, /* Combine the selectors */
.form-submit-button:disabled {
    background-color: #ccc; /* Grayed out */
    cursor: default; /* No pointer */
    opacity: 0.7; /* Slightly transparent */
}